@import "src/styles/variables";
@import "src/styles/mixins";

.block {
	display: flex;
	flex-direction: row;
	align-items: center;

	& button {
		border: 0;
		background: none;
	}

	& form {
		margin: 0 10px 0 10px;
	}

	.block__pages {
		.page {
			margin-left: 7px;
			margin-right: 7px;
			padding: 5px;
		}

		.page_active {
			margin-left: 7px;
			margin-right: 7px;
			padding: 5px;
			color: #F2F5F7;
			background: #21268F;
			border: 1px solid #21268F;
		}
	}

	& span {
		margin-left: 7px;
		margin-right: 7px;

		@include FontSanFrancisco();
		font-style: normal;
		font-weight: 400;
		font-size: 12px;
	}
}