
.layout {
	background: linear-gradient(180deg, #E6D4DE 0%, #9890C7 100%);
	min-height: 100vh;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	//padding-bottom: 20px;

	&__headerWrap {
		width: 100%;
		height: 80px;
	}

	&__header {
		width: 100%;
		height: 60px;
	}

	&__loading {
		width: 100%;
		height: 20px;
	}

	&__main {
		display: flex;
		height: 100%;
		align-items: center;
	}
}