@import "src/styles/variables";
@import "src/styles/mixins";


.modal {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.6);
	z-index: 100;

	&__wrap {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__block {
		width: 395px;
		height: 233px;
		background: #F9F9FE;
		border-radius: 2px;

		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__title {

		height: 57px;
		padding-top: 15px;
		padding-left: 24px;

		@include FontPoppins();
		font-style: normal;
		font-weight: 500;
		font-size: 18px;
		line-height: 27px;
		color: $base-color;

		border-bottom: solid 1px #EDF0F6;
	}

	&__input {
		margin: 0 24px;
	}

	&__btnGroup {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 24px 24px 24px;
	}

	&__button {
		width: 127px;
		height: 36px;
		border-radius: 30px;
		border: none;

		@include FontSanFrancisco();
		font-style: normal;
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
		transition: 0.3s;

		&_cansel {
			color: #21268F;
			opacity: 0.8;
			background: #D7D8EF;
		}

		&_save {
			color: #ECECF9;
			background: #21268F;
			box-shadow: 0 4px 18px rgba(33, 38, 143, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
		}

		&:hover {
			font-size: 18px;
		}
	}

}

